import {areas, DataShape} from "../App";
import React from "react";
import {ResponsiveLine} from "@nivo/line";
import {area, curveMonotoneX} from "d3-shape";
import {Defs} from "@nivo/core";

const ProtectionLayer = ({ series, xScale, yScale, innerHeight }: any) => {
    // console.log(series[0].data);
    // console.log(series);
    const areaGenerator = area()
        .x((d: [any, any]) => {
            return xScale(d['data' as any].x)
        })
        .y0((d: [any, any]) => {
            const data = d['data' as any];
            // return yScale(targetPercent(data.rt, data.y, 0.936, 1))
            // return Math.min(data.y, yScale(targetPercent(data.rt, data.y, 0.936, 1)))
            return yScale(data.y);
        })
        .y1((d: [any, any]) => {
            const data = d['data' as any];
            // return yScale(targetPercent(data.rt, data.y, 0.556, 1)))
            return yScale(data.range);
        })
        .curve(curveMonotoneX)
    // const areaGenerator = area()
    //     .x((d, e) => xScale(d.data.x))
    //     .y0(d => Math.min(innerHeight, yScale(d.data.y - 40)))
    //     .y1(d => yScale(d.data.y + 10))
    //     .curve(curveMonotoneX)

    return (
        <>
            <Defs
                defs={[
                    {
                        id: 'pattern',
                        type: 'patternLines',
                        background: 'transparent',
                        color: '#fc9926',
                        lineWidth: 1,
                        spacing:5,
                        rotation: -45,
                    },
                ]}
            />
            <path
                d={areaGenerator(series[0].data) || undefined}
                fill="url(#pattern)"
                fillOpacity={0.2}
                stroke="#fc9926"
                strokeWidth={0.5}
            />
        </>
    )
}

const Rt = ({data, message}: { data: DataShape, message: string }) => {

    const doublingTime = (rt: number, period: number): number => {
        return period * ( Math.log(2) / Math.log(rt) )
    }

    const today = new Date();
    const thirtyDayCases = data.actualsTimeseries.reduce<number>((acc, val) => {
        const entryDate = new Date(val.date);
        if (entryDate.getTime() + 30*24*60*60*1000 > today.getTime()) {
            if (val.newCases) {
                acc += val.newCases;
            }
        }
        return acc;
    }, 0);
    const thirtyDayDeaths = data.actualsTimeseries.reduce<number>((acc, val) => {
        const entryDate = new Date(val.date);
        if (entryDate.getTime() + 30*24*60*60*1000 > today.getTime()) {
            if (val.newDeaths) {
                acc += val.newDeaths;
            }
        }
        return acc;
    }, 0);

    const casesPerDeath = thirtyDayCases / thirtyDayDeaths;
    const exactChain = Math.log(1 - (casesPerDeath * (1 - data.metrics.infectionRate))) / Math.log(data.metrics.infectionRate);
    const chain = Math.ceil(exactChain);
    const doublingLow = Math.ceil(doublingTime(data.metrics.infectionRate, 5));
    const doublingHigh = Math.ceil(doublingTime(data.metrics.infectionRate, 16));
    const deathLow = Math.ceil(exactChain * 5);
    const deathHigh = Math.ceil(exactChain * 16);

    /**
     * rInit(rt, vacRate, eff) = rt / (1 - (vacRate * eff))
     */
    const rInit = (rt: number, vacRate: number, eff: number): number => {
        return rt / (1 - (vacRate * eff))
    }

    const rtInfection = [{
        id: areas[message],
        data: data.metricsTimeseries.filter(el => el.infectionRate !== null && el.infectionRate !== undefined).map(el => {
            const rate: number = el.infectionRate as number;
            const range = el.vaccinationsCompletedRatio ? rInit(rate, el.vaccinationsCompletedRatio as number, 0.83) : rate;
            return {
                x: el.date,
                y: el.infectionRate,
                range: Math.max(range, rate)
            }
        })
    }];

    const rtMax = rtInfection[0].data.reduce<number>((acc, val) => { return (val.range > acc) ? val.range : acc }, 0);
    const rtLatest = rtInfection[0].data[rtInfection[0].data.length - 1].range;
    const hypDoublingLow = Math.ceil(doublingTime(rtLatest, 5));
    const hypDoublingHigh = Math.ceil(doublingTime(rtLatest, 16));

    return (
        <>
            <div style={{width: 700, height: 300}}>
                <ResponsiveLine
                    markers={[
                        {
                            axis: 'y',
                            value: 1,
                            lineStyle: {stroke: '#b0413e', strokeWidth: 1},
                            // legend: 'y marker at 0',
                            legendPosition: 'bottom-left',
                        },
                    ]}
                    data={rtInfection}
                    margin={{top: 50, right: 140, bottom: 50, left: 60}}
                    xScale={{
                        format: "%Y-%m-%d",
                        type: "time",
                        useUTC: false,
                    }}
                    xFormat="time:%Y-%m-%d"
                    yScale={{type: 'linear', min: 'auto', max: rtMax, stacked: false, reverse: false}}
                    // yFormat=" >-.2f"
                    curve="cardinal"
                    axisTop={null}
                    axisRight={null}
                    axisBottom={{
                        // tickValues: "every 3 month",
                        tickSize: 5,
                        tickPadding: 5,
                        tickRotation: 0,
                        format: "%Y-%m",
                        legend: "Date",
                        legendOffset: 36,
                        legendPosition: "middle"
                    }}
                    axisLeft={{
                        // orient: 'left',
                        tickSize: 5,
                        tickPadding: 5,
                        tickRotation: 0,
                        legend: 'Rt',
                        legendOffset: -45,
                        legendPosition: 'middle'
                    }}
                    enablePoints={false}
                    pointSize={10}
                    pointColor={{theme: 'background'}}
                    pointBorderWidth={2}
                    pointBorderColor={{from: 'serieColor'}}
                    pointLabelYOffset={-12}
                    layers={[
                        'grid',
                        'markers',
                        'areas',
                        ProtectionLayer,
                        'lines',
                        'slices',
                        'axes',
                        'points',
                        'legends',
                    ]}
                    isInteractive={true}
                    // useMesh={true}
                    enableSlices={"x"}
                    sliceTooltip={({slice}) => {
                        return (
                            <div
                                style={{
                                    background: 'white',
                                    padding: '9px 12px',
                                    border: '1px solid #ccc',
                                }}
                            >
                                <div>{slice.points[0].data.xFormatted}</div>
                                {slice.points.map(point => (
                                    <div
                                        key={point.id}
                                        style={{
                                            color: point.serieColor,
                                            padding: '3px 0',
                                        }}
                                    >
                                        <strong>{point.serieId}</strong> {point.data.yFormatted}
                                    </div>
                                ))}
                            </div>
                        )
                    }}
                    legends={[
                        {
                            anchor: 'bottom-right',
                            direction: 'column',
                            justify: false,
                            translateX: 100,
                            translateY: 0,
                            itemsSpacing: 0,
                            itemDirection: 'left-to-right',
                            itemWidth: 80,
                            itemHeight: 20,
                            itemOpacity: 0.75,
                            symbolSize: 12,
                            symbolShape: 'circle',
                            symbolBorderColor: 'rgba(0, 0, 0, .5)',
                            effects: [
                                {
                                    on: 'hover',
                                    style: {
                                        itemBackground: 'rgba(0, 0, 0, .03)',
                                        itemOpacity: 1
                                    }
                                }
                            ]
                        }
                    ]}
                />
            </div>
            <p>
                The current Rt for {areas[message]} expects cases
                to {doublingLow <= 0 ? "halve" : "double"} every <b>{Math.abs(doublingLow)}</b> to <b>{Math.abs(doublingHigh)}</b> days.
                If no one were vaccinated, cases
                would {hypDoublingLow <= 0 ? "halve" : "double"} every <b>{Math.abs(hypDoublingLow)}</b> to <b>{Math.abs(hypDoublingHigh)}</b> days.
            </p>
            {doublingLow > 0 &&
            <p>
                Currently, given the 30-day case rate and death rate in this region, one infection
                will cause a death after <b>{chain}</b> infection generations on average, or
                within <b>{deathLow}</b> to <b>{deathHigh}</b> days if Rt stays steady.
            </p>
            }
        </>
    );
}

export default Rt;