import './App.css';
import {ResponsiveRadar} from '@nivo/radar'
import multnomahData from './data/multnomah.json';
import washingtonData from './data/washington.json';
import clarkData from './data/clark.json';
import honoluluData from './data/honolulu.json';
import larimerData from './data/larimer.json';
import denverData from './data/denver.json';
import elpasoData from './data/elpaso.json';
import TestPositivitySankey from "./component/TestPositivitySankey";
import React, {useState} from "react";
import TestPositivity from "./component/TestPositivity";
import CaseDensity from "./component/CaseDensity";
import Rt from "./component/Rt";
import Vaccination from "./component/Vaccination";
import Icu from "./component/Icu";

const signal = (data: any): string => {
    if ((data.infectionRate > 1) || (data.caseDensity > 10) || (data.testPositivityRatio > 0.05) || (data.icuCapacityRatio >= 0.85)) {
        return 'red';
    }
    return 'green';
}


const signals: Map<string, string> = new Map<string, string>([
    ["multnomah", signal(multnomahData.metrics)],
    ["washington", signal(washingtonData.metrics)],
    ["honolulu", signal(honoluluData.metrics)],
    ["clark", signal(clarkData.metrics)],
    ["larimer", signal(larimerData.metrics)],
    ["denver", signal(denverData.metrics)],
    ["elpaso", signal(elpasoData.metrics)],
]);

const badgeData = [
    {
        'signal': 'rt',
        'multnomah': multnomahData.metrics.infectionRate,
        'washington': washingtonData.metrics.infectionRate,
        'honolulu': honoluluData.metrics.infectionRate,
        'clark': clarkData.metrics.infectionRate,
        'larimer': larimerData.metrics.infectionRate,
        'denver': denverData.metrics.infectionRate,
        'elpaso': elpasoData.metrics.infectionRate,
    },
    {
        'signal': 'caseDensity',
        'multnomah': multnomahData.metrics.caseDensity / 10,
        'washington': washingtonData.metrics.caseDensity / 10,
        'honolulu': honoluluData.metrics.caseDensity / 10,
        'clark': clarkData.metrics.caseDensity / 10,
        'larimer': larimerData.metrics.caseDensity / 10,
        'denver': denverData.metrics.caseDensity / 10,
        'elpaso': elpasoData.metrics.caseDensity / 10,
    },
    {
        'signal': 'testPositivity',
        'multnomah': multnomahData.metrics.testPositivityRatio / 0.05,
        'washington': washingtonData.metrics.testPositivityRatio / 0.05,
        'honolulu': honoluluData.metrics.testPositivityRatio / 0.05,
        'clark': clarkData.metrics.testPositivityRatio / 0.05,
        'larimer': larimerData.metrics.testPositivityRatio / 0.05,
        'denver': denverData.metrics.testPositivityRatio / 0.05,
        'elpaso': elpasoData.metrics.testPositivityRatio / 0.05,
    },
    {
        'signal': 'icuUtilization',
        'multnomah': multnomahData.metrics.icuCapacityRatio / 0.85,
        'washington': washingtonData.metrics.icuCapacityRatio / 0.85,
        'honolulu': honoluluData.metrics.icuCapacityRatio / 0.85,
        'clark': clarkData.metrics.icuCapacityRatio / 0.85,
        'larimer': larimerData.metrics.icuCapacityRatio / 0.85,
        'denver': denverData.metrics.icuCapacityRatio / 0.85,
        'elpaso': elpasoData.metrics.icuCapacityRatio / 0.85,
    }
]

const MyResponsiveRadar = ({data, keys}: any) => (
    <ResponsiveRadar
        data={data}
        keys={keys}
        indexBy="signal"
        maxValue={1.25}
        margin={{top: 80, right: 120, bottom: 100, left: 20}}
        curve="linearClosed"
        borderWidth={1}
        borderColor={{from: 'color'}}
        gridLevels={1.25}
        gridShape="circular"
        gridLabel={(blah) => {
            return null;
        }}
        gridLabelOffset={36}
        enableDots={true}
        dotSize={10}
        // dotColor={{ theme: 'background' }}
        dotColor={(blah) => {
            if (blah.value > 1) {
                return 'red';
            }
            return 'green';
        }}
        dotBorderWidth={2}
        dotBorderColor={(blah) => {
            if (blah.value > 1) {
                return 'red';
            }
            return 'green';
        }}
        enableDotLabel={true}
        dotLabel={(blah) => {
            if (blah.index === "caseDensity") {
                const realValue = blah.value * 10;
                return 'Cases / 100k = ' + parseFloat(realValue.toString()).toFixed(1);
            } else if (blah.index === "testPositivity") {
                const realPercent = blah.value * 0.05 * 100
                return 'Test Positivity: ' + parseFloat(realPercent.toString()).toFixed(1) + "%"
            } else if (blah.index === "icuUtilization") {
                const realPercent = blah.value * 0.85 * 100
                return 'ICU Usage: ' + parseFloat(realPercent.toString()).toFixed(0) + "%"
            }
            return 'Rt: ' + blah.value;
        }}
        dotLabelYOffset={-12}
        // colors={{scheme: 'nivo'}}
        colors={(blah) => {
            return signals.get(blah.key) || 'gray';
        }}
        fillOpacity={0.15}
        blendMode="multiply"
        animate={true}
        // motionConfig="wobbly"
        isInteractive={false}
    />
)

export interface DataShape {
    actuals: any;
    actualsTimeseries: any[];
    annotations: any;
    cdcTransmissionLevel: number;
    cdcTransmissionLevelTimeseries: {
        date: string,
        cdcTransmissionLevel: number
    }[];
    country: string;
    county: string;
    fips: string;
    lastUpdatedDate: string;
    lat: any;
    level: string;
    locationId: string;
    long: any;
    metrics: any;
    metricsTimeseries: any[];
    population: number;
    riskLevels: any;
    riskLevelsTimeseries: any[];
    state: string;
    url: string;
}
export const areas: Record<string, string> = {
    "metro": "Portland Metro Area",
    "multnomah": "Multnomah County",
    "washington": "Washington County",
    "honolulu": "Honolulu County",
    "clark": "Clark County",
    "larimer": "Larimer County",
    "denver": "Denver County",
    "elpaso": "El Paso County",
};

function App() {
    const [message, setMessage] = useState<string>('washington');
    const [data, setData] =  useState(washingtonData);
    const handleChange = async (e: React.ChangeEvent<HTMLSelectElement>) => {
        setMessage(e.target.value);
        setData(await import(`./data/${e.target.value}.json`));
    }

    // console.log(data.metricsTimeseries);

    return (
        <div className="App">
            <div style={{width: 700}}>
                <select defaultValue={message}
                        onChange={handleChange}
                >
                    <option value="metro">Portland Metro Area, OR</option>
                    <option value="multnomah">Multnomah County, OR (Portland)</option>
                    <option value="washington">Washington County, OR</option>
                    <option value="honolulu">Honolulu County, HI</option>
                    <option value="clark">Clark County, NV (Las Vegas)</option>
                    <option value="larimer">Larimer County, CO (Fort Collins)</option>
                    <option value="denver">Denver County, CO</option>
                    <option value="elpaso">El Paso County, CO (Colorado Springs)</option>
                </select>
                <p>
                    ({areas[message]} last updated {data.lastUpdatedDate}. Data is from the CovidActNow
                    API, which comes from the NYTimes data set.)
                </p>
                <p>
                    When trying to measure the impact of COVID in an area, the first thing we need
                    to do is measure how many people have it. Diagnosing this officially requires
                    a test.
                </p>
                <p>
                    Testing, however is a challenge. At any given time, a collection of people is
                    either infected or uninfected, and they each choose whether or not to get tested.
                    (The following graph is a conceptual estimate; numbers not to scale.)
                </p>
                <div style={{width: 700, height: 300}}>
                    <TestPositivitySankey />
                </div>
                <p>
                    "Test positivity" stats only calculate positives and negatives from lab tests,
                    and so they miss undiagnosed cases, as well as home testing. Additionally, as infection
                    prevalence increases, if we don't have enough tests to meet the demand, test positivity
                    increases. Therefore, an increase in test positivity is an indication of an outbreak,
                    even if recorded case count still appears low. For this reason, the CDC says any
                    level above 5% is cause for concern.
                </p>
                <div style={{width: 700, height: 300}}>
                    <TestPositivity data={data} message={message} />
                </div>
                <p>
                    That said, while testing may not capture every case, a rise in test positivity rate
                    does tend to correlate with an increased level of cases. CDC tracks this through "daily
                    new cases per 100k population", and says there is increased virus prevalence when the
                    metric reaches 10 or above.
                </p>
                <div style={{width: 700, height: 300}}>
                    <CaseDensity data={data} message={message} />
                </div>
                <p>
                    An effective reproductive number can be derived from the case count data and other data.
                    The effective reproductive number, or Rt, is how many people an infected person infects
                    on average. Any number above 1 means the outbreak is growing over time. In other words,
                    the cases will double over some time period, more quickly as Rt increases. The shaded
                    area is an estimate of what regional Rt would be if not for the vaccines.
                </p>
                <Rt data={data} message={message} />
                <p>
                    Rt responds to what level of mitigation is in place. Ultimately, without any other
                    mitigation, a disease runs its course by infecting everyone, multiple times if necessary,
                    until sufficient natural immunity is met. In the case of COVID-19, this approach would
                    cause far too much human suffering, including loss of life from other conditions due
                    to overcrowded hospitals. So we look for other forms of mitigation, including masks,
                    social distancing, lockdowns, and especially vaccinations - whatever is sufficient to
                    get an outbreak under control and get Rt under 1.
                </p>
                <p>
                    We can infer a target vaccination level given the current Rt, which changes as the
                    mitigation level changes. For instance, kids returning to school reduces social
                    distancing. As mitigation decreases, Rt increases, and therefore the target vaccination
                    level increases.
                </p>
                <p>
                    Target vaccination levels, indicated with patterned lines below, are determined through
                    herd immunity calculations. The calculation takes into account infection efficacy of vaccines,
                    combined with the reduced probability of vaccinated people passing on the virus to others.
                </p>
                <div style={{width: 700, height: 300}}>
                    <Vaccination data={data} message={message}/>
                </div>
                <p>
                    As a serious disease increases in prevalence, hospitalizations will increase as well.
                    Ultimately, deaths increase too, both from the disease, and from hospitals unable to
                    give routine service due to overcrowding. So we also track ICU capacity. Anything above
                    85% utilization is considered critical:
                </p>
                <div style={{width: 700, height: 300}}>
                    <Icu data={data} message={message}/>
                </div>
                <p>
                    I wanted to come up with a personal indicator that uses each of these signals to
                    help to determine what level of caution I should act with.
                    </p>
                <p>
                    One thing that became clear is that the level of personal risk is different
                    than the level of community risk. Being vaccinated, my risk of serious
                    disease is lower than my risk of passing the virus to others, since viruses
                    have lower efficacy against infection than they do against hospitalization.
                    </p>
                <p>
                    For that reason, when the disease is spreading or has high prevalence, I
                    don't want to contribute to spread. So I'm choosing to limit personal contact
                    - indoors, maskless gatherings with vaccinated people - when any of the
                    above indicators are above the limit.
                    </p>
                <p>The signal is pretty conservative. Others may be comfortable being more social
                    if there is moderate ({'>'} 10) or even substantial ({'>'} 50) prevalence. But generally,
                    if Rt is greater than 1, it means the outbreak will get worse over time, so it's
                    a good signal to be in favor of greater temporary mitigation, until the outbreak
                    is under control again.
                    </p>
                <p>
                    This signal can be drawn visually.
                </p>
                <p>
                    Washington County ({washingtonData.lastUpdatedDate}):
                </p>
                <div style={{width: 700, height: 300}}>
                    <MyResponsiveRadar data={badgeData} keys={['washington']}/>
                </div>
                <p>
                    Multnomah County ({multnomahData.lastUpdatedDate}):
                </p>
                <div style={{width: 700, height: 300}}>
                    <MyResponsiveRadar data={badgeData} keys={['multnomah']}/>
                </div>
                <p>
                    Here are some badges for other counties of family:
                </p>
                <p>
                    Clark County (Las Vegas, NV) ({clarkData.lastUpdatedDate}):
                </p>
                <div style={{width: 700, height: 300}}>
                    <MyResponsiveRadar data={badgeData} keys={['clark']}/>
                </div>
                <p>
                    Honolulu County ({honoluluData.lastUpdatedDate}):
                </p>
                <div style={{width: 700, height: 300}}>
                    <MyResponsiveRadar data={badgeData} keys={['honolulu']}/>
                </div>
                <p>
                    Larimer County (Fort Collins, CO) ({larimerData.lastUpdatedDate}):
                </p>
                <div style={{width: 700, height: 300}}>
                    <MyResponsiveRadar data={badgeData} keys={['larimer']}/>
                </div>
                <p>
                    Denver County ({denverData.lastUpdatedDate}):
                </p>
                <div style={{width: 700, height: 300}}>
                    <MyResponsiveRadar data={badgeData} keys={['denver']}/>
                </div>
                <p>
                    El Paso County (Colorado Springs, CO) ({elpasoData.lastUpdatedDate}):
                </p>
                <div style={{width: 700, height: 300}}>
                    <MyResponsiveRadar data={badgeData} keys={['elpaso']}/>
                </div>
            </div>
        </div>
    );
}

export default App;
